/* Checkboxes */
/* Target the parent container of the disabled input */
.container-class input:disabled {
    /* Style the disabled input */
    opacity: 0.3;
    user-select: none;
    pointer-events: none;
}

/* Style sibling elements within the same parent container */
.container-class *:not(input:disabled) {
    /* Define styles for non-disabled elements */
    /* For example: */
    opacity: 1; /* Reset opacity for non-disabled elements */
    user-select: auto; /* Reset user-select for non-disabled elements */
    pointer-events: auto; /* Reset pointer-events for non-disabled elements */
}


.checkbox {
    position: relative;
    user-select: none;
    margin-bottom: 10px;
    /* margin-left: 35px; */
}

.checkbox input {
    margin-left: 35px;
    transform: scale(1.5); 
}
.checkbox input[type="checkbox"]:checked + .custom-checkbox::after {
  border-color: #ff0000; /* Change the border color of the checkmark to red */
}
.checkbox label {
    position: relative;
