/* Alert */
.alert {
    margin-bottom: 15px;
    display: block;
    padding: 10px 15px;
    border-radius: var(--borderRadius);
    font-weight: 500;
    position: relative;
    cursor: pointer;
}

.alert.non-collapsible {
    &:before,
    &:after {
        content: none;
    }
}

.alert:before,
.alert:after {
    content: "";
    position: absolute;
    width: 4px;
    height: 16px;
    border-radius: 40px;
    right: 30px;
    top: 8px;
}

.alert:before {
    transform: rotate(45deg) translateY(-5.5px) translateX(13.5px);
}

.alert:after {
    transform: rotate(-45deg) translateY(13.5px) translateX(5.5px);
}

