    position: relative;
    display: block;
    width: 100%;
    text-align: center;
    z-index: 10;
}

.tooltip span {
    border-bottom: 1px dotted;
}

.tooltip:after {
    content: attr(data-tooltip);
    background: var(--neutralShade2);
    max-width: 90%;
    width: auto;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    opacity: 0;
    height: auto;
    font-size: 14px;
    padding: 10px;
    border-radius: var(--borderRadius);
    color: var(--foregroundColor);
    text-align: center;
}

.tooltip.dark:after {
    background: var(--neutralShade7);
    color: var(--backgroundColor);
}

.tooltip.top:after {
    bottom: 80%;
    transition: opacity 0.3s ease 0.3s, bottom 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
}

.tooltip.top:hover,
.tooltip.top.hovered {
    &:after {
        bottom: 130%;
        opacity: 1;
    }
}

.tooltip.bottom:after {
    top: 80%;
    transition: opacity 0.3s ease 0.3s, top 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.3s;
}

.tooltip.bottom:hover,
.tooltip.bottom.hovered {
    &:after {
        top: 130%;
        opacity: 1;
    }
}




/* PROFILE CARDS */
.metrics{
