}
.results-header {
    grid-column: span 2;
    display: grid;
    grid-template-columns: 1fr max-content;
}

.results-header__option {
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(2, 1fr);
    cursor: pointer;
}

.option__button {
    display: grid;
    grid-gap: 5px;
    width: 55px;
    height: 75px;
    border-radius: var(--borderRadius);
    padding: 10px;
    overflow: hidden;
    transition: 0.2s ease;
}

.option__button > span {
