    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 16px 16px 45px -15px rgb(252 147 79);

}
.card:hover {
box-shadow: 0px 30px 18px -8px rgba(0, 0, 0,0.1);
  transform: scale(1.10, 1.10);
}

.card__info {
z-index: 2;
background-color: #fff;
border-bottom-left-radius: 12px;
border-bottom-right-radius: 12px;
 padding: 16px 24px 24px 24px;
}

.card__category {
  font-family: 'Montserrat', sans-serif;
  text-transform: capitalize;
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: 700;
  /* font-size: large; */
  color: black;
  /* margin-right: -94px; */
}

.card__title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family:'Montserrat', sans-serif;
}

.card__by {
  font-size: 12px;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
}

.card__author {
  font-weight: 600;
  text-decoration: none;
  color: #AD7D52;
}

.card:hover .card__img--hover {
  height: 100%;
  opacity: 0.1;
  border-radius: 1rem;
  border: 3px solid #be3300;
}

.card:hover .card__info {
  background-color: transparent;
  position: relative;
}

.card:hover .card__info-hover {
  opacity: 1;
}

/* upload project css */
.uploadproject {
  display: flex;
  align-items: center;
  justify-content: center;

}
.projectmethod-radio {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  margin-top: -3px;
  margin-left: 12px;
}

/* input[type="number"],input[type="date"] {

  margin-left: 12px;
  border-radius: 0.3rem;
  border: 2px solid #dbb36a;
} */


.uploadproject-container {
  background-color: #c38e2a38;
  border-radius: 3rem;
  margin: 3rem auto;
  box-shadow: 18px 20px 44px -37px rgb(0 0 0);
  padding: 7px 33px 0px 33px;
}

.uploadproject-title {
  font-size: 24px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  margin-top: 33px;
}

.uploadproject-user__details {
  display: grid;
  gap: 15px;
}

.uploadproject-input__box {
  display: flex;
  flex-direction: column;
}

.uploadproject-details {
  font-size: 20px;
  margin-bottom: 5px;
  text-align: left;
  margin-left: 12px;
  font-weight: 400;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="number"],
input[type="date"],
textarea,
select,
input[type="file"] {
  width: 100%;
  padding: 9px;
  margin-bottom: 10px;
  border: 3px solid #dbb36a;
  border-radius: 1rem;
  box-sizing: border-box;
}

.uploadproject-button {
  margin-bottom: -5rem;
}

input[type="submit"] {
  background-color: #eca21b;
  color: white;
  padding: 10px 15px;
  border: none;
  margin-top: 1rem;
  border-radius: 2rem;
  cursor: pointer;
  font-size: 16px;
}
input[type="submit"]:hover {
  background-color: #734f0d;
}

.selected-technologies {
  margin-top: 20px;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 1rem;
  overflow: auto;
  box-shadow: inset 1px 1px 25px 4px #00000040;
}

.selected-technologies p {
  font-weight: bold;
  margin-bottom: 8px;
}

.selected-technologies ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.selected-technologies li {
  margin-right: 10px;
  margin-bottom: 8px;
  font-size: 16px;
  color: #333;
  background-color: #dbb36a85;
  padding: 6px 12px;
  border-radius: 1rem;
}
